.grid {
  -moz-border-radius: 15px;
  -moz-box-shadow: 0 1px 3px 0 #656565;
  -o-box-shadow: 0 1px 3px 0 #656565;
  -webkit-border-radius: 15px;
  -webkit-box-shadow: 0 1px 3px 0 #656565;
  background-color: rgba(255,255,255,0.8);
  border-radius: 15px;
  box-shadow: 0 1px 3px 0 #656565;
  display: grid;
  grid-template-areas:
    ". . ."
    " . gridData . "
    " . gridPreviousHash . "
    " . . ."
    " . gridHash . "
    " . grid2 . "
    " . . . ";
  grid-template-columns: 24px calc(100% - 48px) 24px;
  grid-template-rows: 24px 50px 46px 7px 46px 53px 24px;
  height: auto;
  max-width: 100%;
  width: 750px;
  opacity: 0.7;
}

.grid:hover {
  -o-box-shadow: 1px 2px 20px 0 #656565;
  -webkit-box-shadow: 1px 2px 20px 0 #656565;
  box-shadow: 1px 2px 20px 0 #656565;
  -webkit-transition: 0.3s ease all;
  -o-transition: 0.3s ease all;
  transition: 0.3s ease all;
}

