.previousHash {
  -ms-flex-align: end;
      align-items: flex-end;
  display: grid;
  grid-area: gridPreviousHash;
  grid-template-areas: "labelPreviousHash previousHash";
  grid-template-columns: 131px calc(100% - 131px);
}

.label#previousHash {
  grid-area: labelPreviousHash;
}

.hash#previous {
  grid-area: previousHash;
}

.hashes {
  -ms-flex-align: start;
      align-items: flex-start;
  display: grid;
  grid-area: gridHash;
  grid-template-areas: "labelPresentHash presentHash";
  grid-template-columns: 54px calc(100% - 54px);
}

.label#presentHash {
  grid-area: labelPresentHash;
}

.hash#present {
  grid-area: presentHash;
}


.grid2 {
  display: grid;
  grid-area: grid2;
  grid-template-areas: "grid3 nounce";
  grid-template-columns: auto 70px;
  grid-template-rows: 54px;
}

.grid3{
  display: grid;
  grid-area: grid3;
  grid-template-areas: "order date";
  grid-template-columns: auto 1fr;
  grid-template-rows: 54px;
  overflow-x: auto;
  white-space: nowrap;
}

