.label {
  font-size: 15px;
  font-weight: 600;
}

p#previous {
  white-space: nowrap;
  overflow: auto;
  font-weight: 400;
}

p#present {
  padding: 0 10px 0 10px;
  white-space: nowrap;
  overflow: auto;
  font-weight: 400;
}


.blockOrder {
  grid-area: order;
  padding-right: 10px;
}

.nounce {
  grid-area: nounce;
  justify-self: center;
  padding-left: 5px;
  padding-right: 5px;

}
.blockDate{
  -ms-flex-item-align: end;
      align-self: flex-end;
  grid-area: date;
  justify-self: start;
  margin-bottom: 4px;
}
.build {
  border-radius: 50%;
  width: 54px;
  height: 54px;
  opacity: 0.7;
}

.material-icons.fs2 {
  font-size: 35px;
  opacity: 0.9;
}

